import { css } from 'lit';

export const styles = css`:host {
  --background: var(--ig-list-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --header-background: var(--ig-list-header-background, var(--ig-gray-900-contrast));
  --header-text-color: var(--ig-list-header-text-color, hsla(var(--ig-secondary-500), var(--ig-secondary-a)));
  --item-background: var(--ig-list-item-background, var(--ig-gray-900-contrast));
  --item-background-hover: var(--ig-list-item-background-hover, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-background-active: var(--ig-list-item-background-active, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-text-color: var(--ig-list-item-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-text-color-hover: var(--ig-list-item-text-color-hover, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-text-color-active: var(--ig-list-item-text-color-active, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --item-title-color: var(--ig-list-item-title-color, 'currentColor');
  --item-title-color-hover: var(--ig-list-item-title-color-hover, 'currentColor');
  --item-title-color-active: var(--ig-list-item-title-color-active, 'currentColor');
  --item-action-color: var(--ig-list-item-action-color, 'currentColor');
  --item-action-color-hover: var(--ig-list-item-action-color-hover, 'currentColor');
  --item-action-color-active: var(--ig-list-item-action-color-active, 'currentColor');
  --item-subtitle-color: var(--ig-list-item-subtitle-color, 'currentColor');
  --item-subtitle-color-hover: var(--ig-list-item-subtitle-color-hover, 'currentColor');
  --item-subtitle-color-active: var(--ig-list-item-subtitle-color-active, 'currentColor');
  --item-thumbnail-color: var(--ig-list-item-thumbnail-color, 'currentColor');
  --item-thumbnail-color-hover: var(--ig-list-item-thumbnail-color-hover, 'currentColor');
  --item-thumbnail-color-active: var(--ig-list-item-thumbnail-color-active, 'currentColor');
  --border-color: var(--ig-list-border-color, hsla(var(--ig-gray-300), var(--ig-gray-a)));
  --border-width: var(--ig-list-border-width, 0.0625rem);
  --border-radius: var(--ig-list-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.1666666667) * 1.5rem), 1.5rem));
  --item-border-radius: var(--ig-list-item-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0) * 1.5rem), 1.5rem));
  color: var(--item-text-color);
  background: var(--item-background);
  border-bottom: 0.0625rem solid var(--border-color);
  border-radius: var(--item-border-radius);
}

:host(:hover),
:host(:focus-within) {
  background: var(--item-background-hover);
}

:host(:last-of-type) {
  border-bottom: none;
}

::slotted(igc-icon) {
  color: var(--item-action-color);
}

[part=title] {
  font-family: var(--ig-body-1-font-family, var(--ig-font-family));
  font-size: var(--ig-body-1-font-size);
  font-weight: var(--ig-body-1-font-weight);
  font-style: var(--ig-body-1-font-style);
  line-height: var(--ig-body-1-line-height);
  letter-spacing: var(--ig-body-1-letter-spacing);
  text-transform: var(--ig-body-1-text-transform);
  margin-top: var(--ig-body-1-margin-top);
  margin-bottom: var(--ig-body-1-margin-bottom);
  color: var(--item-title-color);
}

[part=subtitle] {
  font-family: var(--ig-subtitle-1-font-family, var(--ig-font-family));
  font-size: var(--ig-subtitle-1-font-size);
  font-weight: var(--ig-subtitle-1-font-weight);
  font-style: var(--ig-subtitle-1-font-style);
  line-height: var(--ig-subtitle-1-line-height);
  letter-spacing: var(--ig-subtitle-1-letter-spacing);
  text-transform: var(--ig-subtitle-1-text-transform);
  margin-top: var(--ig-subtitle-1-margin-top);
  margin-bottom: var(--ig-subtitle-1-margin-bottom);
  color: var(--item-subtitle-color);
}`;
